html {
  font-family: sans-serif;
  text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

progress {
  vertical-align: baseline; }

template,
[hidden] {
  display: none; }

a {
  background-color: transparent;
  text-decoration-skip: objects; }

a:active,
a:hover {
  outline-width: 0; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline; }

b,
strong {
  font-weight: bolder; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background-color: #ffff00;
  color: #000000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

svg:not(:root) {
  overflow: hidden; }

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: 1em; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

button,
input,
select,
textarea {
  font: inherit;
  margin: 0; }

optgroup {
  font-weight: bold; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  appearance: none; }

::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54; }

::-webkit-file-upload-button {
  appearance: button;
  font: inherit; }

@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), url("../fonts/montserrat-400.woff2") format("woff2"), url("../fonts/montserrat-400.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), url("../fonts/montserrat-500.woff2") format("woff2"), url("../fonts/montserrat-500.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), url("../fonts/montserrat-600.woff2") format("woff2"), url("../fonts/montserrat-600.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), url("../fonts/montserrat-700.woff2") format("woff2"), url("../fonts/montserrat-700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", "Arial", sans-serif;
  min-width: 320px;
  background-color: #F9FBFD; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.container {
  margin: 0 auto;
  max-width: 1160px;
  width: 84.92%; }
  @media (max-width: 1023px) {
    .container {
      width: auto;
      margin-left: 45px;
      margin-right: 45px; } }
  @media (max-width: 767px) {
    .container {
      margin-left: 15px;
      margin-right: 15px; }
      .container--nojs {
        margin: 0; } }

.header {
  padding-top: 35px;
  padding-bottom: 243px;
  width: 100%;
  background-color: #003067;
  background-image: url("../../img/bg-header-desktop.png");
  background-repeat: no-repeat; }

.header__menu {
  margin-bottom: 210px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__button {
  display: none; }

.header__text {
  padding-left: 95px;
  color: #F9FBFD; }

.header__title {
  margin: 0;
  margin-bottom: 30px;
  font-size: 54px;
  font-weight: 700;
  line-height: 66px; }

.header__description {
  margin: 0;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px; }

@media (max-width: 1023px) {
  .header {
    padding-top: 30px; }
  .header__text {
    padding-left: 0; }
  .header__title {
    font-size: 50px;
    line-height: 61px; }
  .header__description {
    font-size: 18px;
    font-weight: 400;
    line-height: 22px; } }

@media (max-width: 767px) {
  .header {
    padding-top: 20px;
    padding-bottom: 230px;
    position: relative; }
    .header--open {
      padding-top: 236px; }
    .header--nojs {
      background-position-y: bottom; }
  .header__menu {
    flex-wrap: wrap;
    align-items: start; }
    .header__menu--nojs {
      margin-top: -20px;
      margin-bottom: 120px;
      padding-top: 20px;
      padding-left: 15px;
      padding-right: 15px;
      width: auto;
      background-color: #F9FBFD; }
    .header__menu--open {
      margin: 0;
      padding-top: 20px;
      padding-left: 15px;
      padding-right: 15px;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      background-color: #F9FBFD; }
  .header__title {
    font-size: 36px;
    line-height: 45px; }
  .header__description {
    font-size: 16px; }
  .header__button {
    display: block;
    width: 24px;
    height: 14px;
    background-color: transparent;
    background-image: url("../../img/hamburger.svg");
    background-repeat: no-repeat;
    border: 0;
    cursor: pointer; }
    .header__button--close {
      width: 15px;
      height: 15px;
      background-image: url("../../img/cross.svg"); }
    .header__button--nojs {
      display: none; }
  .header__text {
    max-width: 265px; } }

@media (max-width: 1023px) {
  .logo svg {
    width: 38px;
    height: 20px; } }

@media (max-width: 767px) {
  .logo svg {
    width: 42px;
    height: 22px; }
  .logo--nojs {
    margin-bottom: 50px; }
  .logo--dark {
    margin-bottom: 50px; }
  .logo--dark svg {
    fill: #011C40; } }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex; }

.nav__item {
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  margin-right: 33px; }
  .nav__item:last-child {
    margin-right: 0; }

.nav__item a {
  color: #F9FBFD; }

@media (max-width: 1023px) {
  .nav__item {
    font-size: 14px;
    line-height: 17px; }
    .nav__item--footer {
      font-size: 12px;
      line-height: 15px; } }

@media (max-width: 767px) {
  .nav__list {
    flex-direction: column; }
  .nav {
    order: 3;
    flex-basis: 100%;
    display: none; }
    .nav--open {
      margin-bottom: 65px;
      display: block; }
    .nav--open .nav__item {
      margin-bottom: 30px; }
      .nav--open .nav__item:last-child {
        margin-bottom: 0; }
    .nav--open a {
      color: #011C40; }
    .nav--nojs {
      margin-bottom: 65px;
      display: block; }
    .nav--nojs .nav__item {
      margin-bottom: 30px; }
      .nav--nojs .nav__item:last-child {
        margin-bottom: 0; }
    .nav--nojs a {
      color: #011C40; }
    .nav--footer {
      display: block;
      order: 0; }
  .nav__item {
    margin-bottom: 23px; }
    .nav__item:last-child {
      margin-bottom: 0; } }

.footer {
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: #011C40; }

.footer__wrapper {
  display: flex;
  align-items: center; }

.footer__social {
  margin-left: auto; }

.footer__logo {
  margin-right: 40px; }

@media (max-width: 767px) {
  .footer {
    padding-top: 40px;
    padding-bottom: 40px; }
  .footer__wrapper {
    flex-direction: column;
    align-items: start; }
  .footer__logo {
    margin-bottom: 30px; }
  .footer__nav {
    margin-bottom: 30px; }
  .footer__social {
    margin: 0; } }

.social__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center; }

.social__item {
  margin-right: 33px; }
  .social__item:last-child {
    margin-right: 0; }

@media (max-width: 1023px) {
  .social__fb svg {
    width: 9px;
    height: 16px; }
  .social__insta svg {
    width: 16px;
    height: 16px; }
  .social__twit svg {
    width: 16px;
    height: 13px; }
  .social__item {
    margin-right: 23px; }
    .social__item:last-child {
      margin-right: 0; } }

.visually-hidden {
  position: absolute;
  visibility: hidden;
  pointer-events: none;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  text-indent: -9999px;
  white-space: nowrap;
  overflow: hidden; }
